<template>
  <el-dialog 
  title="教师信息" 
  :visible.sync="visible" 
  width="30%">
    <el-form ref="form" :model="form" label-width="80px">
  <el-form-item label="教师姓名">
    <el-input v-model="form.name"></el-input>
  </el-form-item>
  <el-form-item label="性别">
    <el-radio-group v-model="form.gender">
      <el-radio label="男"></el-radio>
      <el-radio label="女"></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="工号">
    <el-input v-model="form.tno"></el-input>
  </el-form-item>
  <el-form-item label="任课班级">
    <el-select v-model="form.clazzName" placeholder="请选择任课班级">
      <el-option label="一班" value="一班"></el-option>
      <el-option label="二班" value="二班"></el-option>
      <el-option label="三班" value="三班"></el-option>
      <el-option label="四班" value="四班"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="电话">
    <el-input v-model="form.telephone"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit">立即创建</el-button>
    <el-button @click="onClose">取消</el-button>
  </el-form-item>
</el-form>
  </el-dialog>
</template>

<script>
import API from "../../utils/service";
export default {
  data() {
      return {
        visible: false,
        id: 0,
        form: {
          name: '',  
          gender: '',
          clazzName: '',
          telephone: '',
          tno: ''
        }
      }
    },
    methods: {
      onSubmit() {
        if(this.id!=0){
          this.$emit('getDetail',this.form,this.id)
          this.id = 0
        }else{
          this.$emit('getDetail',this.form)
        }
        this.form.name = '',
        this.form.gender = '',
        this.form.clazzName = '',
        this.form.telephone = '',
        this.form.tno = '',

         this.visible = !this.visible;
      },
     handleOpen(id=0) {
        if(id!=0){
          this.id = id
        }
      this.visible = !this.visible;
    },
    onClose(){
      this.visible = !this.visible;
    }
    }
};
</script>
